import React, { Component } from "react";
import logo from "./logo.svg";
import "./App.css";
import Add_item from "./components/add_item";
import List from "./components/list";

class App extends Component {
  state = {
    shopping_items: [],
  };

  addItem = (newItem) => {
    this.setState({
      shopping_items: this.state.shopping_items.concat({ item: newItem }),
    });
  };

  render() {
    return (
      <div>
        <Add_item addItem={this.addItem} />
        <table style={{ display: "table-flex" }}>
          <List shopping_items={this.state.shopping_items} />
        </table>
      </div>
    );
  }
}

export default App;
