import React, { Component } from "react";

class Add_item extends Component {
  constructor(props) {
    super(props);
    this.state = { add_item: "" };
  }

  handleChange = (event) => {
    this.setState({ add_item: event.target.value });
  };
  render() {
    return (
      <React.Fragment>
        <input
          className="additem"
          type="text"
          name="itemname"
          value={this.state.add_item}
          onChange={this.handleChange}
        ></input>
        <button
          type="button"
          onClick={() => this.props.addItem(this.state.add_item)}
        >
          Add new Item
        </button>
      </React.Fragment>
    );
  }
}

export default Add_item;
